import React, { Component } from 'react'
import './Register.css'
import Axios from '../../axios'
import { Redirect } from 'react-router-dom'

export default class Register extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            
            type: 'Admin',
            username: '',
            email: '',
            phonenumber: '',
            password: '',
            confirmpassword: '', 
            Registered: false
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange = (e) =>{
        this.setState({[e.target.name]: e.target.value});
    }

    handleSubmit = (e) =>{
        e.preventDefault();

        console.log(this.state);
        Axios.post('/register', this.state)
        .then(response =>{
            console.log(response);
            alert("Successfully Registered..");
            this.setState({
                Registered : true,
                redirect: "/adLogin"
            })
        })
        .catch(error =>{
            console.log(error);
        })

    }

    render() {
        
        const {Registered, redirect} = this.state

         if (Registered === true) {
            return <Redirect to={redirect} />
        } 

        return (
            <div>
                <div className="dash-register">
                    <h3>Regi<span>ster</span></h3>

                    <div className="div-card-row">
                        <div className="div-col-left">
                            
                            <div className="form-box">
                                <div className="div-label">
                                    <label>Name</label>
                                </div>
                                <div className="div-input">
                                    <input class="form-control" type="text" name="username" onChange={this.handleChange} value={this.username} required/>
                                </div>
                            </div>
                            
                            <div className="form-box">
                                <div className="div-label">
                                    <label>Email</label>
                                </div>
                                <div className="div-input">
                                    <input class="form-control" type="email" name="email" onChange={this.handleChange} value={this.email} required/>
                                </div>
                            </div>
                            
                            <div className="form-box">
                                <div className="div-label">
                                    <label>Number</label>
                                </div>
                                <div className="div-input">
                                    <input class="form-control" type="number" name="phonenumber" onChange={this.handleChange} value={this.phonenumber} required/>
                                </div>
                            </div>
                             
                        </div>

                        <div className="div-col-right">

                            <div className="form-box">
                                <div className="div-label">
                                    <label>Password</label>
                                </div>
                                <div className="div-input">
                                    <input class="form-control" type="password" name="password" onChange={this.handleChange} value={this.password} required/>
                                </div>
                            </div>
                            
                            <div className="form-box">
                                <div className="div-label">
                                    <label>Confirm Password</label>
                                </div>
                                <div className="div-input">
                                    <input class="form-control" type="password" name="confirmpassword" onChange={this.handleChange} value={this.confirmpassword} required/>
                                </div>
                            </div>
                            <div className="div-card">
                                <button type="submit" onClick={this.handleSubmit} class="submit-btn">Submit</button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
