import React, { Component } from 'react'
import '../Admin-css/Admin-Detail.css'
import Edit from './EditUser'
import Message from './Message'
import Alert from './AlertMessage'

import Axios from '../../../axios'

export default class ViewBlog extends Component {

    constructor (props) {
        super(props)

        this.state = {

            users: [],
            type: 'User',
            username: '',
            email: '',
            phonenumber: '',
            password: '',
            confirmpassword: '',

            id: '',

            show: false,
            message: false,
            editMode: false,

            responseData: '',

            
            formErrors: {},
        }
     
        this.handleChange = this.handleChange.bind(this);
        this.handleUpdate = this.handleUpdate.bind(this);

        this.closeModal = this.closeModal.bind(this);
        this.openModal = this.openModal.bind(this);

        this.openMessage = this.openMessage.bind(this);
        this.closeMessage = this.closeMessage.bind(this);

        this.closeAlertMessage = this.closeAlertMessage.bind(this);
        
        this.refreshPage = this.refreshPage.bind(this);

        this.handleFormValidation = this.handleFormValidation.bind(this);
    }

    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    }

    refreshPage() {
        window.location.reload(false);
    }

    componentDidMount() {

        Axios.get('/getUser')
            .then(response => {

                this.setState({ users: response.data })
            })
            .catch(error => {
                console.log(error)
            })
    }


    openModal = (e) => {
        this.setState({
            show: true
        })
    }

    closeModal = (e) => {
        this.setState({
            show: false
        })
    }

    openMessage = (e) => {
        this.setState({
            message: true,
        })
    }

    closeMessage = (e) => {
        this.setState({
            message: false,
        })
        this.refreshPage();
    }

    openAlertMessage = (id) => {
        this.setState({
            alert: true,
            id: id,
        })
    }

    closeAlertMessage = (e) => {
        this.setState({
            alert: false,

        })
    }


    handleFormValidation() {
        const formErrors = {};
        let isValid = true;

        const { username, phonenumber } = this.state

        if (username === '') {
            formErrors.usernameError = "Username is Required!!";
            isValid = false;
        }

        if (phonenumber === '') {
            formErrors.phonenumberError = "Phone Number is Required!!";
            isValid = false;
        }



        if (username.length === 1) {
            formErrors.usernameError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (username.length === 2) {
            formErrors.usernameError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (username.length === 3) {
            formErrors.usernameError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (username.length > 250) {
            formErrors.usernameError = "Title must be of length 250";
            isValid = false;
        }
        if (username.match(/[!@/$%^&*(),.?":{}|<>]/)) {
            formErrors.usernameError = "No Special Characters";
            isValid = false;
        }


        this.setState({ formErrors });
        return isValid;
    }


    deleteUser(id) {

        // console.log(this.state);
        Axios.delete(`/deleteUser/${ id }`)
            .then(response => {

                this.setState({
                    alert: false,
                    responseData: response.data.message
                })

                this.openMessage();
            })
            .catch(error => {
                console.log(error);
               
            })
    }

    edituser = async (id) => {


        let editingItem = this.state.users.find(user => {
            return user.id === id;
        })

        console.log(editingItem)
      
        await this.setState({
            username: editingItem.username,

            phonenumber: editingItem.phonenumber,
        })

        this.openModal();

    }


    handleUpdate(e) {
        e.preventDefault();

        const isValid = this.handleFormValidation();
        if (isValid) {

            let id = this.state.editMode;

            this.setState({
                editMode: true,
            })

                const data = {
                    username: this.state.username,
                    phonenumber: this.state.phonenumber,
                }
                Axios.put(`/edituser/${ id }`, data)
                    .then(response => {

                        this.closeModal();

                        this.setState({
                            responseData: response.data.message
                        })

                        this.openMessage();
                    })

                    .catch(error => {
                        console.log(error);

                    })
            
        }
    }

   


    render() {

        const { formErrors, users, responseData } = this.state

        const showHide = this.state.show ? "edit-form display-block" : "edit-form display-none";
        const messageHide = this.state.message ? "message-box display-block" : "message-box display-none";
        const alertHide = this.state.alert ? "message-box display-block" : "message-box display-none";

        return (
            <>
                <div className="class-table">
                    <div className="section-title">
                        <h5>User List</h5>
                    </div>
                    <table>
                        <thead>
                            <tr>
                                <th>User Name</th>
                                <th>Email</th>
                                <th>Phone Number</th>
                                <th>Address</th>
                                <th>Action</th>
                            </tr>
                        </thead>
                        <tbody>

                            {
                                users.length ?
                                    users.map(user => <tr key={ user.id }>
                                        <td>{ user.username }</td>
                                        <td>{ user.email }</td>
                                        <td>{ user.phonenumber }</td>
                                        <td>{ user.address }</td>

                                        <td>
                                            <div class="action-btns">
                                                <button class="div-action-btn" onClick={ this.edituser.bind(this, user.id) }><i class="fas fa-pen"></i></button>
                                                <button class="div-action-btn" onClick={ this.openAlertMessage.bind(this, user.id) } ><i class="fas fa-trash"></i></button>
                                            </div>
                                        </td>
                                    </tr>) : null
                            }

                        </tbody>
                    </table>
                    <Edit show={ this.state.show } handleClose={ this.closeModal } handleSubmit={ this.handleUpdate } showHide={ showHide }>
                        
                        <div className="div-input">
                            <input class="form-control" type="text" name="username" onChange={ this.handleChange } value={ this.state.username } placeholder="Username" />
                            { formErrors.usernameError &&
                                <div class="input-errorMsg">{ formErrors.usernameError }</div>
                            }
                        </div>
                        <div className="div-input">
                            <input class="form-control" type="number" name="phonenumber" onChange={ this.handleChange } value={ this.state.phonenumber } placeholder="Phone Number" />
                            { formErrors.phonenumberError &&
                                <div class="input-errorMsg">{ formErrors.phonenumberError }</div>
                            }
                        </div>
                    </Edit>

                    <Message show={ this.state.message } messageHide={ messageHide }>
                        <h3>Success !</h3>
                        <p>{ responseData }</p>
                        <div className="m-footer">
                            <button type="button" onClick={ this.closeMessage } className="btn-footer">Ok</button>
                        </div>
                    </Message>

                    <Alert show={ this.state.alert } alertHide={ alertHide }>
                        <h3>Are You Sure<br /><span>You want to Delete this?</span></h3>
                        <div className="m-footer">

                            <button type="button" onClick={ this.deleteUser.bind(this, this.state.id) } className="btn-footer">Ok</button>
                            <button type="button" onClick={ this.closeAlertMessage } className="btn-footer">Cancel</button>
                        </div>
                    </Alert>
                </div>

            </>
        )
    }
}
