import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

import './App.css'


import Login from './components/Login/Login'
import AdminLogin from './components/Main/Login/AdminLogin'
import Register from './components/Main/Register'
import Logout from './components/UserDashboard/Logout'
import AdLogout from './components/AdminDashboard/Logout'
import Dashboard from './components/AdminDashboard/Dashboard'
import UserDashboard from './components/UserDashboard/Dashboard'



class App extends Component {
  render() {
    return (
      <Router>
        <div>
        
          <Switch>  
            <Route exact path="/login" as component={Login}/>
            <Route exact path="/adLogin" as component={AdminLogin}/>
            <Route exact path="/main" as component={Register}/>
            <Route path="/logout" as component={Logout}/>
            <Route path="/adlogout" as component={AdLogout}/>
            <Route path="/admin_dashboard" as component={Dashboard}/>
            <Route path="/user_dashboard/Home" as component={UserDashboard}/>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;