import React, { Component } from 'react'
import Axios from '../../../../axios'
import '../../Admin-css/Admin-Detail.css'
import Message from './Message'

export default class AddNews extends Component {

    constructor (props) {
        super(props)
        let setImage = 0

        this.state = {
            
            ncategories: [],

            title: '',
            news_category: '',
            news_subcategory: '',
            content: '',
            image: null,
            source: '',

            message: '',

            responseData: '',

            formErrors: {},

            setImage,
            
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.openMessage = this.openMessage.bind(this);
        this.closeMessage = this.closeMessage.bind(this);

        this.onUpload = this.onUpload.bind(this);

        this.refreshPage = this.refreshPage.bind(this);
        this.handleFormValidation = this.handleFormValidation.bind(this);
    }

    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    }

    refreshPage() {
        window.location.reload(false);
    }

    openMessage = (e) => {
        this.setState({
            message: true,
        })
    }

    closeMessage = (e) => {
        this.setState({
            message: false,
        })
        this.refreshPage();
    }

    onUpload(e) {
        // let file = e.target.files[0];
        this.setState({
            image: e.target.files[0],
            setImage: 1,
        })
        console.log(e.target.files[0]);
    }

    componentDidMount(){

        Axios.get('/getNewsCategory')
            .then(response => {
                // console.log(response)
                this.setState({ ncategories: response.data })
            })
            .catch(error => {
                console.log(error)
            })
    }

    handleFormValidation() {
        const formErrors = {};
        let isValid = true;

        const { title, news_category, content } = this.state

        if (title === '') {
            formErrors.titleError = "News Title is Required!!";
            isValid = false;
        }
        if (news_category === '') {
            formErrors.news_categoryError = "News Category is Required!!";
            isValid = false;
        }
        
        if (content === '') {
            formErrors.contentError = "Description is Required!!";
            isValid = false;
        }
        

        if (title.length === 1) {
            formErrors.titleError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (title.length === 2) {
            formErrors.titleError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (title.length === 3) {
            formErrors.titleError = "Title must be of of 4 or more";
            isValid = false;
        }
        if (title.length > 250) {
            formErrors.titleError = "Title must be of length 250";
            isValid = false;
        }
       

        if (content.length > 2000) {
            formErrors.contentError = "Description must be of length 2000";
            isValid = false;
        }
        if (content.length < 10) {
            formErrors.contentError = "Description must be of 10 or more";
            isValid = false;
        }


        this.setState({ formErrors });
        return isValid;
    }

    handleSubmit = (e) => {
        e.preventDefault();

        const isValid = this.handleFormValidation();

        if (isValid) {
            if (this.state.setImage === 1) {

                let data = new FormData();

                data.append('title', this.state.title);
                data.append('news_category', this.state.news_category);
                data.append('news_subcategory', this.state.news_subcategory);
                data.append('content', this.state.content);
                data.append('image', this.state.image);
                data.append('source', this.state.source);

                console.log(this.state);
                Axios.post(`/addNews/${ this.state.setImage}`, data)
                    .then(response => {
                        console.log(response);
                        this.setState({
                            successMsg: true,

                        })
                        this.refreshPage();
                    })

                    .catch(error => {
                        console.log(error);
                    })
            }
            else if (this.state.setImage === 0) {

                const data = {
                    title: this.state.title,
                    news_category: this.state.news_category,
                    news_subcategory: this.state.news_subcategory,
                    content: this.state.content,
                    image: this.state.image,
                    source: this.state.source,

                }

                Axios.post('/addNews', data)
                    .then(response => {
                        console.log(response);
                        this.setState({
                            successMsg: true,

                        })
                        this.refreshPage();
                    })

                    .catch(error => {
                        console.log(error);
                    })

            }
        }

    }

    render() {

        const { formErrors, ncategories, responseData } = this.state
        const messageHide = this.state.message ? "message-box display-block" : "message-box display-none";

        if (!this.props.show) {
            return null;
        }

        return (
            <div className={ this.props.showHide }>
                <div className="modal-wrapper" >
                    <div className="modal-header">
                        <h4>Add News Details</h4>
                        <i class="fal fa-times" onClick={ this.props.handleClose }></i>
                    </div>
                    <div className="modal-content">
                        <div className="modal-body">
                            <div className="grid-3-col">
                                <div className="form-box">
                                    
                                    <div className="div-input">
                                        <input class="form-control" type="text" name="title" onChange={ this.handleChange } value={ this.title }  placeholder="Title"/>
                                        { formErrors.titleError &&
                                            <div class="input-errorMsg">{ formErrors.titleError }</div>
                                        }
                                    </div>
                                </div>
                                <div className="form-box">
                                    
                                    <div className="div-input">
                                        <select class="form-control" onChange={ this.handleChange } name="news_category" placeholder="Category">
                                            <option>Select Category</option>{
                                                ncategories.length ?
                                                    ncategories.map(category =>

                                                        <option name={ category.news_category } value={ category.news_category } key={ category.nc_id }>{ category.news_category }</option>
                                                    ) : null
                                            }
                                        </select>
                                        { formErrors.news_categoryError &&
                                            <div class="input-errorMsg">{ formErrors.news_categoryError }</div>
                                        }
                                    </div>
                                </div>

                                <div className="form-box">
                                    
                                    <div className="div-input">
                                        <input class="form-control" type="text" name="news_subcategory" onChange={ this.handleChange } value={ this.news_subcategory } placeholder="Sub Category"/>
                                    </div>
                                </div>

                                <div className="form-box">
                                   
                                    <div className="div-input">
                                        <input class="form-control" type="text" name="source" onChange={ this.handleChange } value={ this.source } placeholder="Source Link" />
                                    </div>
                                </div>
                                <div className="form-box">
                                    
                                    <div className="div-input">
                                        <textarea rows="1" class="form-control" type="text" name="content" onChange={ this.handleChange } value={ this.content } placeholder="Description"/>
                                        { formErrors.contentError &&
                                            <div class="input-errorMsg">{ formErrors.contentError }</div>
                                        }
                                    </div>
                                </div>
                                <div className="form-box">
                                    
                                    <div className="div-input">
                                        <input class="form-control" type="file" onChange={ this.onUpload } name="image" value={ this.image } accept=".png, .jpg" placeholder="Image"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="submit" onClick={ this.handleSubmit } class="btn-footer">Submit</button>
                            <button type="button" onClick={ this.props.handleClose } className="btn-footer">Cancel</button>
                        </div>
                    </div>
                </div>
                <Message show={ this.state.message } messageHide={ messageHide }>
                    <h3>Success !</h3>
                    <p>{ responseData }</p>
                    <div className="m-footer">
                        <button type="button" onClick={ this.closeMessage } className="btn-footer">Ok</button>
                    </div>
                </Message>
            </div>
        )
    }
}
